#header {
    height: 85px; 
    width: 100%;  
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.356); 
    background-color: #FFF700;  

    .title {
        font-size: 30px; 
        font-weight: 800;
    }

    .links {
        font-size: 20px; 
        font-weight: 800; 
        text-decoration: underline; 

        a {
            padding: 0 1rem; 
        }
    }

    .close {
        position: absolute; 
        top: 15px;  
        right: 15px; 
        font-size: 20px; 
        z-index: 2;
    } 

    hr {
        border: 1px solid black; 
        width: 50%; 
        margin: 0 auto;
    }
} 

