.burgermenu, .burgerclickoutside {
    position: absolute; 
    top: 0;
    left: 0; 
    transition: left 0.5s; 
}

.burgermenu {
    z-index: 1002;
}

.burgerclickoutside {
    z-index: 1001; 
    background-color: rgb(0 0 0 / 21%);
    visibility: visible; 
    opacity: 1; 
    transition: opacity 0.3s;
}

.burgermenu.closed {
    left: -720px
} 

.burgerclickoutside.closed {
    visibility: hidden; 
    opacity: 0;
}