.imageLabel{position:relative;cursor:pointer}.imageLabel .overlay{position:absolute;inset:0;background-color:rgba(0,0,0,0);height:100%;width:100%;transition:background-color .3s;border-radius:5px}.imageLabel .imageInputLabel{position:relative;width:100%;border-radius:5px;padding:.6rem .9rem;margin-bottom:.9rem;font-size:1rem;background-color:#f2f2ff;border:1px solid #b5b5ff;font-family:"Montserrat",sans-serif;resize:none;max-height:400px}.imageLabel .imageInputLabel:focus{outline:none;border:1px solid #000fbf}.imageLabel .imageInputLabel input{visibility:hidden;display:none}.imageLabel .imageInputLabel img{width:100%;object-fit:contain}.imageLabel .delete{padding:1.2rem;content:"❌";position:absolute;top:0px;right:0px;font-size:2rem;z-index:100000}.imageLabel::after{content:"ZMĚNIT";position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:#fff;font-size:1.5rem;font-weight:700;opacity:0;transition:opacity .3s;pointer-events:none}.imageLabel:hover .overlay{background-color:rgba(0,0,0,.6)}.imageLabel:hover::after{opacity:1}.addNewImage{background-color:#f2f2ff;border:1px solid #b5b5ff}