  background-color: #f2f2ff;
  border: 1px solid #b5b5ff;
  font-family: "Montserrat", sans-serif;
  resize: none;
}
input:focus, .select:focus,
textarea:focus {
  outline: none;
  border: 1px solid #000FBF;
}

.select {
  padding: 0;
}
