.singleSkatepark .carousel{height:500px}@media(max-width: 992px){.singleSkatepark .carousel{height:300px}}.singleSkatepark .details{display:flex;flex-direction:column;justify-content:space-between;height:100%}.singleSkatepark .details .copy{position:relative}.singleSkatepark .details .copy::after{content:"kliknutím zkopíruješ";position:absolute;top:-30px;right:0px;opacity:0;z-index:2;transition:opacity .15s;background-color:rgba(0,0,0,.048);padding:5px 10px;width:max-content}.singleSkatepark .details .copy:hover::after{opacity:1}@media(max-width: 992px){.singleSkatepark .comment{border-bottom:1px solid #000}}.singleSkatepark .comment .right{border-left:1px solid #000}@media(max-width: 992px){.singleSkatepark .comment .right{border-left:unset}}